<template>
  <div class="container">
    <div class="timer-controls">
      <button @click="changeState">{{ (!timerActive) ? 'Старт' : 'Стоп' }}</button>
      <button v-bind:disabled="(timerActive)" @click="reset">Сбросить</button>
    </div>
    <div class="timer">
      <span ref="timer">{{time}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "TimerT",
  data(){
    return {
      timerActive : false,
      seconds : 0,
      minutes : 0,
      hours : 0,
      ms : 0,
      time : "00:00:00:00"
    }
  },
  methods : {
    changeState(){
      this.timerActive = !this.timerActive;
    },
    render(){
      if(this.ms === 99) { this.seconds++; this.ms = 0}
      if(this.seconds === 60) { this.minutes++; this.seconds = 0}
      if(this.minutes === 60) { this.hours++; this.minutes = 0}
      this.time = `${(this.hours < 10) ? `0${this.hours}` : this.hours}:${(this.minutes < 10) ? `0${this.minutes}` : this.minutes}:${(this.seconds < 10) ? `0${this.seconds}` : this.seconds}:${(this.ms < 10) ? `0${this.ms}` : this.ms}`;
    },
    reset(){
      this.seconds = 0;
      this.minutes = 0;
      this.hours = 0;
      this.ms = 0;
      this.render();
    }
  },
  mounted() {
    setInterval(()=>{
      if(!this.timerActive) return;
      this.ms++;

      this.render();
    },10)
  }
}
</script>

<style scoped>
  .timer{
    font-size: 5vh;
    color: #fff;
  }
  .container{
    padding: 7vh 0;
    display: flex;
    flex-direction: column;
    position: relative;

  }

  .timer-controls{
    display: flex;
    column-gap: 1vw;
  }
</style>